import { Grid, Typography } from '@material-ui/core';
import React from 'react'
import { useStyles } from './HeaderImage.style';


interface HeaderImageProps {
    header: string;
    content: string;
    src?: string;
}


export const HeaderImage: React.FC<HeaderImageProps> = ({ header, content, src }) => {
    const classes = useStyles();
    return (
        <Grid container item justify="center" className={classes.whiteBg}>
            <Grid item lg={5}>
                <Typography
                    className={classes.mainTxt}
                    style={{ textAlign: 'center', marginTop: '80px', maxWidth: '600px' }}
                >
                    {header}
                </Typography>
            </Grid>
            <Grid item xs={12} style={{ display: 'flex', justifyContent: 'center' }}>
                <Typography className={classes.subContent} style={{ textAlign: 'center', maxWidth: '600px' }}>
                    {content}
                </Typography>
            </Grid>
            <Grid item xs={12}>
                <img
                    src={src || 'https://c.bonfireassets.com/images/step-design-tool.jpg'}
                    style={{ width: '100%', height: '100%', marginBottom: '80px' }}
                />
            </Grid>
        </Grid>
    );
};
