import { Grid } from '@material-ui/core'
import React from 'react'
import { useStyles } from './Feature.style'

interface FeatureProps {
    iconSrc: string;
    header: string;
    content: string;
    style?: object;
}


export const Feature: React.FC<FeatureProps> = ({ iconSrc, header, content, style }) => {
    const classes = useStyles()
    return (
        <Grid item style={style} lg={4} className={classes.featureCont}>
            <Grid item lg={12}><img src={iconSrc} /> </Grid>
            <Grid item lg={12} className={classes.featureHeader}>{header}</Grid>
            <Grid item lg={12} className={classes.featureContent}>{content}</Grid>
        </Grid>
    )
}