import React from 'react';
import authorImg from '../../img/emily.jpg';

interface FeedbacProps {
    feedback: string;
    authorName: string;
    authorProfession: string;
}

const Feedback: React.FC<FeedbacProps> = ({ feedback, authorName, authorProfession }) => {
    return (
        <section className="feedback">
            <div className="feedback__content">
                <div className="feedback__feedback">
                    <span>{feedback}</span>
                </div>
                <div className="author">
                    <img src={authorImg} alt="author img" className="author__img" />
                    <div className="author__info">
                        <h3 className="author__name">{authorName}</h3>
                        <h4>{authorProfession}</h4>
                    </div>
                </div>
            </div>
        </section>
    );
};
export default Feedback;
