import React from 'react';
import { makeStyles, Paper, Grid, Typography } from '@material-ui/core';
import clsx from 'clsx';
interface TimeLineBoxProps {
    header?: string;
    header2?: string;
    content2?: string;
    content?: string;
    src?: string;
    right?: boolean;
}

const useStyles = makeStyles((theme) => ({
    leftBoard: {
        borderRadius: 0,
        width: '95%',
        boxShadow: '1px 0px 10px #b2c1c1',
        position: 'relative',
        '&:after': {
            content: "''",
            width: '20px',
            height: '20px',
            position: 'absolute',
            zIndex: 999,
            borderTop: '20px solid transparent',
            borderBottom: '20px solid transparent',
            borderLeft: '20px solid #fff',
            right: -20,
            top: '50%',
            transform: 'translateY(-50%)',
        },
    },
    rightBoard: {
        width: '100%',
        marginLeft: '5%',
        '&:after': {
            left: -20,
            borderLeft: 'none',
            borderRight: '20px solid #fff',
        },
    },
    boardHeader: {
        fontSize: '2rem',
        paddingTop: '5px',
        marginBottom: '10px',
        fontWeight: 600,
        fontFamily: 'Soleil, Helvetica, Arial, sans-serif',
        textAlign: 'center',
    },
    boardHeader2: {
        fontSize: '1.7rem',
        marginBottom: '10px',
        fontWeight: 600,
        fontFamily: 'Soleil, Helvetica, Arial, sans-serif',
        textAlign: 'center',
    },
    boardContent: {
        fontFamily: 'roboto, sans-serif',
        textAlign: 'center',
        lineHeight: '1.5',
    },
    boardImg: {
        borderRadius: '50%',
        width: '100px',
        height: '100px',
    },
    board: {
        padding: `${theme.spacing(6)}px`,
    },
}));

export const TimeLineBox: React.FC<TimeLineBoxProps> = ({
    header,
    content,
    content2,
    header2,
    right,
    src = 'https://c.bonfireassets.com/images/step1-supply.png',
}) => {
    const classes = useStyles();
    return (
        <Paper className={clsx(classes.leftBoard, right && classes.rightBoard)}>
            <Grid container xs={12} className={classes.board}>
                <Grid item lg={3}>
                    <img className={classes.boardImg} src={src} />
                </Grid>
                <Grid item lg={9}>
                    <Typography className={classes.boardHeader}>{header}</Typography>
                    <Typography className={classes.boardHeader2}>{header2}</Typography>
                    <Typography variant="h4" className={classes.boardContent}>
                        {content}
                    </Typography>
                    <Typography variant="h5" className={classes.boardContent}>
                        {content2}
                    </Typography>
                </Grid>
            </Grid>
        </Paper>
    );
};
